// Create custom element wrapper for Wix
class SophosicChatElement extends HTMLElement {
  constructor() {
    super();
    this.attachShadow({ mode: 'open' });
  }

  async connectedCallback() {
    try {
      // Create container in shadow DOM first
      const container = document.createElement('div');
      container.id = 'sophosic-chat-container-' + Math.random().toString(36).substr(2, 9);
      this.shadowRoot.appendChild(container);

      // Load Vue first and wait for it to be ready
      await this.loadScript('https://unpkg.com/vue@3/dist/vue.runtime.global.prod.js');
      
      // Then load our app and wait for it to be ready
      await this.loadScript('/sophia-web/js/app.js');
      
      // Wait a bit to ensure everything is initialized
      await new Promise(resolve => setTimeout(resolve, 100));

      // Get configuration
      const config = {
        apiUrl: this.getAttribute('api-url') || 'https://n8n.sophosic.ai/webhook/e60704a9-52c1-47eb-9037-2344d6b56c21/chat',
        theme: {
          primary: this.getAttribute('primary-color') || '#5423E7',
          secondary: this.getAttribute('secondary-color') || '#1E1B3C'
        }
      };

      // Check dependencies
      if (!window.Vue || !window.SophosicChat?.mount) {
        throw new Error('Required dependencies not loaded');
      }

      // Mount the widget
      window.SophosicChat.mount(container, config);
    } catch (err) {
      console.error('Failed to load chat widget:', err);
      // Add visual error feedback
      this.shadowRoot.innerHTML = `
        <div style="color: red; padding: 1em; border: 1px solid red; margin: 1em;">
          Failed to load chat widget: ${err.message}
        </div>
      `;
    }
  }

  loadScript(src) {
    return new Promise((resolve, reject) => {
      if (document.querySelector(`script[src="${src}"]`)) {
        resolve();
        return;
      }
      const script = document.createElement('script');
      script.src = src;
      script.onload = () => setTimeout(resolve, 100);
      script.onerror = reject;
      document.head.appendChild(script);
    });
  }
}

// Only define if not already defined
if (!customElements.get('sophosic-chat-widget')) {
  customElements.define('sophosic-chat-widget', SophosicChatElement);
} 